}
.btn1 {
  position: relative;
  bottom: 35px;
  margin: 0;
  margin-bottom: 35px;
}
.bottom {
  display: table;
  position: fixed;
  bottom: 0;
  height: 30px;
  width: 230px;
  font-size: 14px;
  padding: 0 12px;
  text-align: right;
  background: #001529;
  cursor: pointer;
}
.bottom .icon_fold {
  color: #fff;
  display: table-cell;
  vertical-align: middle;
}
.bottom .icon_fold_left {
  color: #fff;
  display: table-cell;
  vertical-align: middle;
  text-align: left;
}
.bottom .icon_fold_right {
  color: #fff;
  display: table-cell;
  vertical-align: middle;
  text-align: right;
}
.bottom_collapsed {
  display: table;
  position: fixed;
  bottom: 0;
  height: 30px;
  width: 230px;
  font-size: 14px;
  padding: 0 12px;
  text-align: right;
  background: #001529;
  cursor: pointer;
  width: 64px;
}
.bottom_collapsed .icon_fold {
  color: #fff;
  display: table-cell;
  vertical-align: middle;
}
.bottom_collapsed .icon_fold_left {
  color: #fff;
  display: table-cell;
  vertical-align: middle;
  text-align: left;
}
.bottom_collapsed .icon_fold_right {
  color: #fff;
  display: table-cell;
  vertical-align: middle;
  text-align: right;
}
.bottom_collapsed .icon_fold_left {
  text-align: center;
}
.menu_icon {
  width: 25px;
  height: 25px;
  margin-right: 10px;
  vertical-align: middle;
  position: relative;
  top: -2px;
  display: inline-block;
}
.menu_icon_active {
  width: 25px;
  height: 25px;
  margin-right: 10px;
  display: inline-block;
  vertical-align: middle;
  position: relative;
  top: -2px;
  display: none;
}
.submenu_cover {
  width: 100%;
  height: 100%;
}
.submenu_cover:hover .menu_icon,
.submenu_cover_active .menu_icon {
  display: none;
}
.submenu_cover:hover .menu_icon_active,
.submenu_cover_active .menu_icon_active {
  display: inline-block;
}
.collapsedItem {
  padding: 5px 10px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.collapsedItem:hover {
  color: #4fb6ff;
}
