.imgBox {
  position: relative;
}
.imgContainer {
  margin: 5px 0;
  text-align: left;
}

.imgContainer img {
  box-sizing: border-box;
  display: inline-block;
  width: 100px;
  height: 100px;
  margin: 5px 2px;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  cursor: pointer;
}

